  width: 29%;
  height: 51%;
  background: url(../svg/play-vid.svg) no-repeat center/cover;
  display: block;
  position: absolute;
  left: 50%;
  z-index: 1;
  top: 50%;
  transform: translate(-50%, -50%);
}
.vid::before {
  content: "";
  display: inline-block;
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.15);
  border-radius: 15px;
}

