  }
}
@keyframes mascotte {
  0% {
    opacity: 0;
    top: 25%;
  }
  20% {
    top: 25%;
  }
  60% {
    top: 0;
    opacity: 1;
  }
  100% {
    opacity: 1;
    top: 0;
  }
}
@keyframes letterb {
  0% {
    opacity: 0;
    transform: translateX(-10%);
    background-position: 10% center;
  }
  20% {
    opacity: 0;
    transform: translateX(-10%);
  }
  50% {
    opacity: 1;
    transform: translateX(0);
  }
  70% {
    background-position: center;
    background-position: 10% center;
  }
  90% {
    opacity: 1;
    transform: translateX(0%);
    background-position: 1% center;
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
    background-position: 1% center;
  }
}
@keyframes letterl {
  0% {
    opacity: 0;
    transform: translateX(-8%);
    background-position: 10% center;
  }
  20% {
    opacity: 0;
    transform: translateX(-8%);
  }
  50% {
    transform: translateX(0);
    opacity: 1;
  }
  70% {
    background-position: 10% center;
  }
  90% {
    transform: translateX(0);
    opacity: 1;
    background-position: 1% center;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
    background-position: 1% center;
  }
}
@keyframes lettera {
  0% {
    opacity: 0;
    transform: translateX(4%);
    background-position: 10% center;
  }
  20% {
    opacity: 0;
    transform: translateX(4%);
  }
  50% {
    opacity: 1;
    transform: translateX(0);
  }
  70% {
    background-position: 17% center;
  }
  90% {
    opacity: 1;
    background-position: 7% center;
  }
  100% {
    opacity: 1;
