// Kiosque

.kiosque{
    position: relative;
    padding-top: 5rem;
    padding-bottom: 2rem;
    .see_all {
        position: absolute;
        right: 0;
        top: 33px;
        background: transparent;
        border-bottom: solid 2px #000;
        border-radius: 0px;
        a{
            padding: 0 0 0.2rem 0;
            color: #000;
        }
    }
    .section-title{
        margin-bottom: 6rem;
    }
    &:after{
        content: "";
        position: absolute;
        top: 0;
        bottom: 25%;
        left: 50%;
        right: 0;
        width: 100vw;
        transform: translateX(-50%);
        background:transparent linear-gradient(180deg, #FFCB04 0%, #FFB81C 100%) 0% 0% no-repeat padding-box;
        z-index: -1;
    }
    .section-container{
        position: relative;
        &:after{
            content: "";
            background: #fff;
            width: 100vw;
            position: absolute;
            bottom: 0;
            top: 68%;
            left: 50%;
            margin-left: -50vw;
        }
    }
    a{
        text-decoration: none;
    }
    .swiper_title{
        h3{
            font-size: 16px;
            color: #868686;
            text-align: center;
            font-weight: 400;
            text-decoration: none;
            margin-top: 1rem;
        }
    }
}



/* Albums */
div#body-section-albums-photos a {
    text-decoration: none;
}
div#body-section-albums-photos a h3{
    text-align: center;
    font-size: 18px;
}
@include media-breakpoint-up(md){
    a#section-bla-en-images ~ .section-container {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
    }
}
#body-section-bla-en-images a.table_image span {
    font-weight: 500;
    border-bottom: 2px solid #ffb81c;
    white-space: nowrap;
}


/* citoyenneté page d'accueil */

@media screen and (min-width: 600px){
    .citoyennete-2 div#body-section-texte-citoyennete {
        width: 106%;
        left: -21%;
        margin-top: 26%;
        height: 25rem;
        position: absolute;
    }
    div#body-section-liens-citoyennete {
        width: 36.4%;
        margin-left: 2rem;
    }
}

#content-core .citoyennete-1 {
    padding-top:0;
    margin-top: 7rem;
    height: 31rem;
    background-position: top;
    background-position: left;
    border-radius: 0 30px 0 0;
}
.citoyennete-1 h2.hidden-section-title,
.citoyennete-2 h2.hidden-section-title,
.section-liens-citoyennete h2.hidden-section-title{
    display: none !important;
}
.citoyennete-1 #body-section-citoyennete div.text {
    padding-top: 9%;
    max-width: 22rem;
    margin-left: 3rem;
}
.citoyennete-1 h2 {
    color: white;
    text-transform: uppercase;
    font-size: 1.25rem;
    text-shadow: 0 0 14px lightgrey;
}

.citoyennete-1 p {
    font-weight: 600;
    margin-bottom: 0;
    padding-bottom: 1rem;
    color: white;
    font-size: 3rem;
    line-height: 3.5rem;
    text-shadow: 0 0 14px rgba(0,0,0,0.2);
}
#content-core .citoyennete-2{
    padding-top:0;
    margin-top: 7rem;
    position: relative;
}
.citoyennete-2 div#body-section-texte-citoyennete {
    background: #378ed1;
    overflow: hidden;
    padding: 5rem;
    color: white;
}
.section-liens-citoyennete.sectionlinks{
    padding-top:0;
    margin-bottom:0;
    background-color:transparent !important;
}
.section-liens-citoyennete.sectionlinks:after{
    display:none;
}
.section-liens-citoyennete div#body-section-liens-citoyennete {
    background: #f7f7f7;
    padding: 1rem;
    position: relative;
}
.section-liens-citoyennete #body-section-liens-citoyennete ul {
    padding: 1.5rem 2rem 1.25rem 3rem;
}
.section-liens-citoyennete #body-section-liens-citoyennete ul li {
    line-height: 3rem;
    font-weight: 600;
}
.section-liens-citoyennete .table_title span{
    position:relative;
}
.section-liens-citoyennete .table_title span::before {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #ffbb1c;
    left: 0;
    right: 0;
    position: absolute;
    bottom: -0.5rem;
}
/* publications */

.kiosque .swiper_title .h3, .kiosque .swiper_title h3 {
    color: #262626;
    font-weight: 500;
}

#body-section-publications .swiper_description {
    text-align: center;
    color: #747474;
}



#body-section-mon-bla ul li .table_display a.table_image:before,
#body-section-annuaire ul li .table_display a.table_image:before,
#body-section-les-sites-de-la-ville ul li .table_display a.table_image:before{
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}


/* MEDIA QUERIES */

//MON BLA
@include media-breakpoint-down(xl) {
    #body-section-section-monbla-images .section-monbla-img-container img.section-monbla-image2 {
    width: 100%;
    }
    .section-mon-bla, .mon-bla-liens {
        width: 50%;
    }
    #body-section-mon-bla ul {
        grid-template-columns: repeat(auto-fill,minmax(165px,1fr));
    }
    #body-section-section-monbla-images .section-monbla-img-container img.section-monbla-logo {
        position: absolute;
        left: 10%;
        width: 20%;
    }
    #body-section-section-monbla-images .section-monbla-img-container img.section-monbla-image1 {
        position: absolute;
        bottom: 0;
        left: 10%;
        width: 40%;
    }
}
